<template>
  <div>
    <v-data-table
      :headers="headers"
      :items="admins"
      :items-per-page="15"
      style="border: 1px solid #ddd; padding: 10px 0 0;"
    >
      <template v-slot:top>
        <v-toolbar flat dense>
          <v-spacer></v-spacer>
          <v-dialog v-model="dialog" max-width="400px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn outlined v-bind="attrs" v-on="on">
                <v-icon left dark> mdi-plus </v-icon>
                Add
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="12" md="12">
                      <v-text-field
                        v-model="editedItem.id"
                        label="ID"
                      ></v-text-field>
                      <v-text-field
                        v-model="editedItem.pw"
                        label="PWD"
                      ></v-text-field>
                      <v-text-field
                        v-model="editedItem.pwconfirm"
                        label="PWD Confirm"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn outlined @click="save">
                  Save
                </v-btn>
                <v-btn outlined @click="close">
                  Cancel
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="400px">
            <v-card>
              <div class="card_title text-center mb-8">
                Are you sure you want to delete this admin?
              </div>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" @click="deleteItemConfirm"
                  >Delete</v-btn
                >
                <v-btn color="primary" @click="closeDelete">Cancel</v-btn>
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:item.actions="{ item }">
        <v-btn v-if="item.edit" small outlined class="mr-2" @click="editItem(item)">
          Edit
        </v-btn>
        <v-btn small outlined @click="deleteItem(item)"> Delete </v-btn>
      </template>
      <template v-slot:no-data> </template>
    </v-data-table>
  </div>
</template>

<script>
export default {
  data: () => ({
    dialog: false,
    dialogDelete: false,
    headers: [
      {
        text: "No",
        align: "start",
        value: "number",
      },
      {
        text: "Authority",
        value: "name",
      },
      {
        text: "ID",
        value: "id",
      },
      { text: "Last Access Date", value: "date",  },
      { text: "", value: "actions", align: "end", sortable: false },
    ],
    admins: [
      {
        number: 1,
        name: "Super",
        id: "SuperAdmin",
        date: "2021.09.17",
        pw: '123214',
        pwconfirm: '123214',
        edit: false
      },
      {
        number: 2,
        name: "Admin",
        id: "NormalAdmin",
        date: "2021.09.16",
        pw: '123214123214',
        pwconfirm: '123214123214',
        edit: true
      },
    ],
    model: [],
    subPart: [],
    editedIndex: -1,
    editedItem: {
      name: "",
    },
    defaultItem: {
      name: "",
    },
  }),
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? "New" : "Edit";
    },
  },

  watch: {
    dialog(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },

  methods: {
    editItem(item) {
      this.editedIndex = this.admins.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.admins.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.admins.splice(this.editedIndex, 1);
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (this.editedIndex > -1) {
        Object.assign(this.admins[this.editedIndex], this.editedItem);
      } else {
        this.admins.push(this.editedItem);
      }
      this.close();
    },
  },
};
</script>

